<template>
  <b-modal
    :id="identifier"
    :class="`plg-modal ${styles}`"
    hide-footer
    :title="title"
  >
    <slot></slot>
  </b-modal>
</template>

<script>
export default {
  name: 'Modal',
  props: [
    'identifier',
    'styles',
    'title'
  ],
  data () {
    return {
    }
  }
}
</script>

<style scoped>

</style>

<style>
  .plg-modal .modal.show .modal-dialog {
    width: 100%;
    max-width: initial;
    margin: 0;
  }

  .plg-modal.dark .modal-content {
    background: #000;
    color: #fff;
  }

  .plg-modal.dark .close {
    color: #fff;
  }
</style>



