
<template>
  <div class="form-group form-item-group">
    <select
      :id="id"
      :name="id"
      :value="value"
      ref="field"
      class="form-control form-item-input"
      @change="updateInput()"
    >
      <slot></slot>
    </select>
    <label :for="id" class="form-item-label">{{label}}</label>
  </div>
</template>

<script>
export default {
  name: 'Select',
  props: [
    'id',
    'label',
    'placeholder',
    'required',
    'value'
  ],
  methods: {
    updateInput () {
      this.$emit('input', this.$refs.field.value)
    }
  }
}
</script>

<style scoped lang="scss">
  .form-item-group {
    position: relative;
    margin-bottom: 1.5rem;
  }

  .form-item-input:not([size]):not([multiple]) {
    height: 50px;
  }

  .form-item-label {
    position: absolute;
    top: 0;
    padding: 16px 0 0 20px;
    transition: all 200ms;
    opacity: 0;
    margin-bottom: .5rem;
    display: inline-block;
    font-size: 60%;
    transform: translate3d(0, -33%, 0);
    opacity: 1;
  }

</style>



