
<template>
  <div
    class="form-group form-item-group"
    v-bind:class="{'invalid': !getValidStatus}"
  >
    <input
      :id="id"
      :name="id"
      :placeholder="placeholder||label"
      :value="value"
      ref="field"
      :type="type"
      class="form-control form-item-input"
      @change="updateInput()"
    >
    <label :for="id" class="form-item-label">{{label}}</label>
    <div v-if="invalidMsg" class="invalid-msg">{{invalidMsg}}</div>
  </div>
</template>

<script>
export default {
  name: 'BasicInput',
  props: [
    'id',
    'label',
    'placeholder',
    'value',
    'isValid',
    'invalidMsg',
    'type'
  ],
  computed: {
    getValidStatus () {
      return this.isValid && this.$refs.field.value !== ''
    }
  },
  methods: {
    updateInput () {
      this.$emit('input', this.$refs.field.value)
    }
  }
}
</script>

<style scoped lang="scss">
  .form-item-group {
    position: relative;
    margin-bottom: 1.5rem;
  }

  .form-item-label {
    position: absolute;
    top: 0;
    padding: 16px 0 0 17px;
    transition: all 200ms;
    opacity: 0;
    margin-bottom: .5rem;
    display: inline-block;
  }

  .form-item-input:focus + .form-item-label {
    font-size: 60%;
    transform: translate3d(0, -33%, 0);
    opacity: 1;
  }

</style>



