<template>
  <div class="form-wrapper">
    <div class="member-details-form" v-bind:class="{'validated' : formData.validated}">
      <div class="form-row">
        <div class="col">
          <SelectInput id="title" label="Title" v-model="formData.title.value">
            <option value="mr">Mr</option>
            <option value="mrs">Mrs</option>
          </SelectInput>
        </div>
        <div class="col">
          <SelectInput id="gender" label="Gender" v-model="formData.gender.value">
            <option value="male">Male</option>
            <option value="female">Female</option>
          </SelectInput>
        </div>
      </div>
      <div class="form-row">
        <div class="col">
          <BasicInput
            id="fname"
            label="First name"
            type="text"
            v-model="formData.fname.value"
            :isValid="formData.fname.isValid"
            :invalidMsg="formData.fname.invalidMsg"
            />
        </div>
        <div class="col">
          <BasicInput
            id="lname"
            label="Last name"
            type="text"
            v-model="formData.lname.value"
            :isValid="formData.lname.isValid"
            :invalidMsg="formData.lname.invalidMsg"
            />
        </div>
      </div>
      <div class="form-row">
        <div class="col">
          <BasicInput
            id="suburb"
            label="Suburb"
            type="text"
            v-model="formData.suburb.value"
            :isValid="formData.suburb.isValid"
            :invalidMsg="formData.suburb.invalidMsg"
            />
        </div>
        <div class="col">
          <BasicInput
            id="pcode"
            label="Post code"
            type="number"
            v-model="formData.pcode.value"
            :isValid="formData.pcode.isValid"
            :invalidMsg="formData.pcode.invalidMsg"
          />
        </div>
        <div class="col">
          <SelectInput
            id="state"
            placeholder="State"
            label="State"
            v-model="formData.state.value"
          >
            <option value="vic">VIC</option>
            <option value="nsw">NSW</option>
          </SelectInput>
        </div>
      </div>
      <div class="form-row">
        <div class="col">
          <BasicInput
            id="email"
            label="Email"
            v-model="formData.email.value"
            :isValid="formData.email.isValid"
            :invalidMsg="formData.email.invalidMsg"
          />
        </div>
      </div>
      <div class="form-row">
        <div class="col">
          <BasicInput
            id="phone"
            label="Contact number"
            type="text"
            v-model="formData.phone.value"
            :isValid="formData.phone.isValid"
            :invalidMsg="formData.phone.invalidMsg"
          />
        </div>
        <div class="col">
          <BasicInput
            id="dob"
            label="Date of birth"
            type="date"
            v-model="formData.dob.value"
            :isValid="formData.dob.isValid"
            :invalidMsg="formData.dob.invalidMsg"
          />
        </div>
      </div>
      <div class="form-row">
        <div class="col">
          <CheckBox
            id="marketing"
            label="I want to receive margeting...."
            v-model="formData.marketing.value"
            :isValid="formData.marketing.isValid"
            />
          <CheckBox
            id="promotion"
            label="I want to receive gaming...."
            v-model="formData.promotion.value"
            :isValid="formData.promotion.isValid"
            />
          <CheckBox
            id="privacy"
            v-model="formData.privacy.value"
            :isValid="formData.privacy.isValid"
            v-if="!isUpdating"
            >
            <span slot="labelSlot">
              I have read and understood and accept the <b-btn v-b-modal.modal-privacy class="btn-label-link">privacy policy</b-btn>
            </span>
          </CheckBox>
          <CheckBox
            id="rules"
            v-model="formData.rules.value"
            :isValid="formData.rules.isValid"
            v-if="!isUpdating"
            >
            <span slot="labelSlot">I am over the age of 18 and am an Australian Resident. I have carefully read and understood the
              <b-btn v-b-modal.modal-rules class="btn-label-link">rules of the program</b-btn> and agree to strictly abide by these
              rules as amended from time to time and declare that teh details in this application are true and correct
            </span>
          </CheckBox>
        </div>
      </div>
      <SignaturePad v-if="!isUpdating" label="Signature" ref="signaturePad" />
      <button v-on:click="submit" class="btn-default">{{ isUpdating ? 'Update' : 'Sign up'}}</button>
    </div>
    <Modal identifier="modal-rules" title="Rules of the program">
      <RulesInfo />
    </Modal>
    <Modal identifier="modal-privacy">
      <PrivacyInfo />
    </Modal>
  </div>
</template>

<script>
import Modal from '@/components/elements/Modal'
import BasicInput from '@/components/form-elements/BasicInput'
import SelectInput from '@/components/form-elements/SelectInput'
import CheckBox from '@/components/form-elements/CheckBox'
import SignaturePad from '@/components/form-elements/SignaturePad'
import RulesInfo from '@/components/information/RulesInfo'
import PrivacyInfo from '@/components/information/PrivacyInfo'

export default {
  name: 'MemberDetailsForm',
  components: {
    BasicInput,
    SelectInput,
    CheckBox,
    SignaturePad,
    Modal,
    RulesInfo,
    PrivacyInfo
  },
  methods: {
    submit () {
      this.validateForm()
      return false
      // const signature = this.$refs.signaturePad.getSigString()
      // TODO: request functionality
    },
    validateForm () {
      this.formData.validated = true
      this.formData.fname.isValid = this.formData.fname.value !== ''
      this.formData.lname.isValid = this.formData.lname.value !== ''
      this.formData.suburb.isValid = this.formData.suburb.value !== ''
      this.formData.email.isValid = this.isEmailValid()
      this.formData.phone.isValid = this.isPhoneValid()
      this.formData.pcode.isValid = this.isPCodeValid()
      this.formData.dob.isValid = this.formData.dob.value !== ''
      this.formData.promotion.isValid = true
      this.formData.marketing.isValid = true
      this.formData.privacy.isValid = this.formData.privacy.value === true
      this.formData.rules.isValid = this.formData.rules.value === true
    },
    isEmailValid () {
      const email = (this.formData.email.value).toLowerCase()
      /* eslint-disable no-useless-escape */
      const reg = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      return reg.test(email)
    },
    isPhoneValid () {
      const phone = this.formData.phone.value
      /* eslint-disable no-useless-escape */
      const reg = /^\({0,1}((0|\+61)(2|4|3|7|8)){0,1}\){0,1}(\ |-){0,1}[0-9]{2}(\ |-){0,1}[0-9]{2}(\ |-){0,1}[0-9]{1}(\ |-){0,1}[0-9]{3}$/
      return reg.test(phone)
    },
    isPCodeValid () {
      const pcode = this.formData.pcode.value
      const reg = /^[0-9]{4}$/
      return reg.test(pcode)
    }
  },
  data () {
    return {
      errors: [],
      isUpdating: this.$store.state.currentMember,
      formData: {
        validated: false,
        fname: { value: '', isValid: false, invalidMsg: 'First name is required' },
        lname: { value: '', isValid: false, invalidMsg: 'Last name is required' },
        title: { value: 'mr', isValid: true },
        gender: { value: 'female', isValid: true },
        suburb: { value: '', isValid: false, invalidMsg: 'Suburb is required' },
        pcode: { value: '', isValid: false, invalidMsg: 'Invalid postcode' },
        state: { value: 'nsw', isValid: true },
        email: { value: '', isValid: false, invalidMsg: 'Invalid email' },
        phone: { value: '', isValid: false, invalidMsg: 'Contact number is required' },
        dob: { value: '', isValid: false, invalidMsg: 'DOB is required' },
        marketing: { value: true, isValid: true },
        promotion: { value: true, isValid: true },
        privacy: { value: false, isValid: true },
        rules: { value: false, isValid: true }
      }
    }
  }
}
</script>

<style lang="scss">

  .member-details-form.validated .form-control:invalid {
    border-color: $error-color;
  }

  .form-group .invalid-msg {
    display: none;
  }

  .member-details-form.validated .form-group.invalid .invalid-msg {
    display: block;
    color: $error-color;
    font-size: 70%;
    margin-left: 15px;
    margin-top: 3px;
  }

  .form-item-input {
    color: $body-color;
    border: 1px solid #ccc;
    padding: 15px 15px 10px;
  }

  .form-item-input:focus {
    border: 1px solid #444;
    box-shadow: none;
  }

  .member-details-form .btn-label-link:hover,
  .member-details-form .btn-label-link:active,
  .member-details-form .btn-label-link:focus,
  .member-details-form .btn-label-link {
    background: transparent;
    border: none;
    color: $body-color;
    font-family: 'bentonsansbold';
    text-decoration: underline;
    padding: 0;
    margin: 0;
    vertical-align: inherit;
    font-size: inherit;
    line-height: inherit;
    outline: none;
    box-shadow: none;
  }
</style>



