<template>
  <div class="row">
    <div class="container">
      MemberInterests
      <div
        v-for="interest in interestsData"
        v-bind:key="interest.id"
        :class="`progress-icon icon-${index}`"
        ref="icons"
        ></div>
      <button v-on:click="submit" class="btn-default">Submit</button>
    </div>
  </div>
</template>

<script>
// import axios from 'axios'
// const API_URL = 'https://mrcplgmemberformstest.azurewebsites.net/api/referencedata'

export default {
  name: 'MemberInterests',
  mounted () {
    // this.getData()
  },
  methods: {
    // getData () {
    //   axios.get(API_URL)
    //     .then(({ data }) => {
    //       console.log('interests', data)
    //       this.interestsData = data
    //     })
    //     .catch(err => {
    //       this.error(err)
    //     })
    // },
    error (err) {
      console.error('Failed', err)
    },
    submit: function () {
      if (this.$store.state.currentMember !== undefined) {
        this.$router.push('process-complete')
      } else {
        this.$router.push('verification-required')
      }
    }
  },
  data () {
    return {
      interestsData: []
    }
  }
}
</script>

<style scoped lang="scss">
</style>



