
<template>
  <div class="form-group form-item-group">
    <div class="form-item-label">{{label}}</div>
    <div class="form-item-signature">
      <div class="btn-clear" v-on:click="clearSig"></div>
      <vueSignature class="signature-pad" ref="signature" :sigOption="sigOptions"></vueSignature>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SignaturePad',
  props: [
    'id',
    'label',
    'value'
  ],
  methods: {
    clearSig () {
      this.$refs.signature.clear()
      this.hasSignature = false
    },
    getSigString () {
      if (!this.hasSignature) {
        return false
      }
      return this.$refs.signature.save()
    },
    beginDraw () {
      this.hasSignature = true
    }
  },
  data () {
    const beginDraw = this.beginDraw
    return {
      hasSignature: false,
      sigOptions: {
        penColor: '#000',
        onBegin: function () {
          return beginDraw()
        }
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .form-item-group {
    position: relative;
    display: flex;
    height: 120px;
    background: $form-bg;
  }

  .form-item-label {
    background: #ccc;
    flex: 0 0 31%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    font-family: 'bentonsansbold'
  }

  .form-item-signature {
    flex: 0 0 69%;
    position: relative;
  }

  .signature-pad {
    width: 100%;
  }

  .btn-clear {
    position: absolute;
    right: 10px;
    top: 5px;
    width: 20px;
    height: 20px;
    color: #000;
    font-size: 25px;
    &:after {
      content: '\27F3';
    }
  }

</style>



