
<template>
  <div
    class="form-group form-item-group"
    v-bind:class="{'invalid': !isValid}"
  >
    <input
      :id="id"
      type="checkbox"
      :checked="value"
      ref="field"
      class="form-control form-item-input"
      @change="updateInput()"
    >
    <label :for="id" class="form-item-label" >
      <slot v-if="!label" name="labelSlot"></slot>
      <span v-if="label">{{label}}</span>
    </label>

  </div>
</template>

<script>
export default {
  name: 'CheckBox',
  props: [
    'id',
    'label',
    'placeholder',
    'required',
    'value',
    'isValid'
  ],
  methods: {
    updateInput () {
      this.$emit('input', this.$refs.field.checked)
    }
  }
}
</script>

<style scoped lang="scss">
  .form-item-group {
    position: relative;
    margin-bottom: 3px;
    font-size: 80%;
  }

  .form-item-input {
    position: absolute;
    left: -9999px;
  }

  .form-item-label {
    padding-left: 40px;
  }

  .form-item-input + .form-item-label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 20px;
    height: 20px;
    border: 1px solid $body-color;
    color: $body-color;
    background: #fff;
    border-radius: 2px;
  }

  .form-item-input:checked + .form-item-label:before {
    color: #fff;
    background: $body-color;
  }

  .invalid .form-item-input + .form-item-label:before {
    border-color:  $error-color;
  }

  .form-item-input:checked + .form-item-label:after{
    content: '';
    position: absolute;
    background-size: contain;
    background-color: #fff;
    mask-image: url('~@/assets/images/check.svg');
    top: 4px;
    left: 5px;
    font-size: 1.4em;
    line-height: 0.8;
    color: #fff;
    transition: all .2s;
    width: 10px;
    height: 10px;
    display: block;
  }

</style>



