<template>
  <div class="verify-req dark-page">
    <div class="row">
      <div class="container">
        All done
        <button v-on:click="reset" class="btn-default">reset</button>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'ProcessComplete',
  methods: {
    reset: function () {
      this.$store.state.currentMember = undefined
      this.$router.push('/')
    }
  }
}
</script>

<style scoped lang="scss">
  .verify-req {
    text-align: center;
    display: flex;
    flex-direction: column;
  }
</style>



