<template>
    <div class="page-nav-wrapper">
      <div class="row">
        <div class="container">
            <div class="page-nav">
            <router-link
              v-if="backRef"
              :to="backRef"
              class="btn-back btn-nav"
            ></router-link>
            <h1 v-if="pageTitle" class="title">{{pageTitle}}</h1>
            <router-link
              v-if="forwardRef"
              :to="forwardRef"
              class="btn-forward btn-nav"
            >Skip</router-link>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'NavigationBar',
  props: [
    'backLink',
    'forwardLink',
    'title',
    'updateOptions'
  ],
  computed: {
    forwardRef: function () {
      const updateOptions = this.updateOptions
      const isUpdating = this.$store.state.currentMember
      if (isUpdating && updateOptions !== undefined && updateOptions.forwardLink !== undefined) {
        return updateOptions.forwardLink
      }
      return this.forwardLink
    },
    backRef: function () {
      const updateOptions = this.updateOptions
      const isUpdating = this.$store.state.currentMember
      if (isUpdating && updateOptions !== undefined && updateOptions.backLink !== undefined) {
        return updateOptions.backLink
      }
      return this.backLink
    },
    pageTitle: function () {
      const updateOptions = this.updateOptions
      const isUpdating = this.$store.state.currentMember
      if (isUpdating && updateOptions !== undefined && updateOptions.title !== undefined) {
        return updateOptions.title
      }
      return this.title
    }
  },
  data () {
    return {
    }
  }
}
</script>

<style scoped>
  .page-nav-wrapper {
    background: #fff;
    border-bottom: 1px solid #ccc;
    height: 100px;
  }

  .page-nav {
    display: flex;
  }

  .title {
    flex: 0 0 80%;
    text-align: center;
    text-transform: uppercase;
    line-height: 100px;
  }

  .btn-back:after {
    transform: rotate(270deg);
  }

  .btn-forward:after {
    transform: rotate(90deg);
    right: 10px;
  }

  .btn-nav {
    text-decoration: none;
    color: #000;
    flex: 0 0 10%;
    position: relative;
    line-height: 100px;
  }

  .btn-nav:after {
    content: '\005e';
    position: absolute;
    left: 10px;
  }

</style>



