<template>
  <div class="landing full-container">
    <div class="row row-top">
      <div class="container">
        <div class="content">
          <div class="logo"><img :src="logoImage"></div>
          <div class="tag-line"><img :src="tagImage"></div>
          <router-link :to="'member-form'" class="landing-btn btn-default -gold">Join today</router-link>
          <div class="divider">OR</div>
          <router-link :to="'member-search'" class="landing-btn btn-default -gold">Update your details</router-link>
        </div>
      </div>
    </div>
    <div class="row row-btm">
      <div class="container">
        <b-btn v-b-modal.more-info class="btn-more">
          Find out more
        </b-btn>
      </div>
    </div>
    <Modal :identifier="modalId" styles="dark">
      <PlgRewardsInfo />
    </Modal>
  </div>
</template>

<script>
import Modal from '@/components/elements/Modal'
import PlgRewardsInfo from '@/components/information/PlgRewardsInfo'
import logoImage from '../../assets/images/pegasus-logo.png'
import tagImage from '../../assets/images/collect-redeem-experience.png'

export default {
  name: 'Landing',
  components: {
    Modal,
    PlgRewardsInfo
  },
  mounted () {
    this.$store.state.currentMember = undefined
  },
  data () {
    return {
      modalId: 'more-info',
      logoImage: logoImage,
      tagImage: tagImage
    }
  }
}
</script>

<style scoped>
  .row-top {
    flex-grow: 2;
  }

  .row-btm {
    flex-grow: 1;
    margin-top: 0;
  }

  .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    align-items: center;
  }

  .logo {
    margin: 0 auto 5px;
  }

  .tag-line {
    margin: 0 auto 100px;
  }

  .divider {
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px auto;
  }

  .divider:before,
  .divider:after {
    width: 100px;
    content: '';
    border-top: 1px solid #707070;
    margin: 0 15px;
  }

  .landing-btn {
    width: 250px;
    color: #000;
  }

  .btn-more {
    background: transparent;
    font-weight: bold;
    border: none;
    padding-right: 28px;
    position: relative;
  }

  .btn-more:after {
    content: '';
    display: inline-block;
    height: 13px;
    width: 20px;
    background-size: contain;
    background-color: #fff;
    mask-image: url('~@/assets/images/chev-down.svg');
    position: absolute;
    right: 0;
    top: 10px;
  }

  .btn-secondary.btn-more:active,
  .btn-secondary.btn-more:focus,
  .btn-secondary.btn-more:hover,
  .btn-secondary.btn-more:hover:not(:disabled):not(.disabled) {
    outline: none;
    background: transparent;
    box-shadow: none;
    border: none;
  }

</style>



