import Vue from 'vue'
import Router from 'vue-router'
import Landing from '@/components/pages/Landing'
import MemberApplication from '@/components/pages/MemberApplication'
import MemberInterests from '@/components/pages/MemberInterests'
import MemberSearch from '@/components/pages/MemberSearch'
import VerificationRequired from '@/components/pages/VerificationRequired'
import ConfirmVerification from '@/components/pages/ConfirmVerification'
import Error from '@/components/pages/Error'
import StaffLogin from '@/components/pages/StaffLogin'
import ReviewMember from '@/components/pages/ReviewMember'
import ProcessComplete from '@/components/pages/ProcessComplete'
import NavigationBar from '@/components/elements/NavigationBar'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'landing',
      component: Landing,
      meta: { bodyClass: 'dark-scheme' }
    },
    {
      path: '/member-form',
      name: 'member-form',
      components: {
        default: MemberApplication,
        pageNav: NavigationBar
      },
      props: {
        pageNav: {
          backLink: '/',
          title: 'Membership application',
          updateOptions: {
            backLink: '/member-search',
            title: 'Update details'
          }
        }
      },
      meta: { bodyClass: 'mid-scheme' }
    },
    {
      path: '/member-search',
      name: 'member-search',
      components: {
        default: MemberSearch,
        pageNav: NavigationBar
      },
      props: {
        pageNav: {
          backLink: '/',
          title: 'Member search'
        }
      }
    },
    {
      path: '/member-interests',
      name: 'member-interests',
      components: {
        default: MemberInterests,
        pageNav: NavigationBar
      },
      props: {
        pageNav: {
          backLink: '/member-form',
          forwardLink: 'verification-required',
          title: 'Member interests',
          updateOptions: {
            forwardLink: '/process-complete'
          }
        }
      }
    },
    {
      path: '/verification-required',
      name: 'verification-required',
      component: VerificationRequired
    },
    {
      path: '/confirm-verification',
      name: 'confirm-verification',
      component: ConfirmVerification
    },
    {
      path: '/error',
      name: 'error',
      component: Error
    },
    {
      path: '/review-member',
      name: 'review-member',
      component: ReviewMember
    },
    {
      path: '/staff-login',
      name: 'staff-login',
      meta: { bodyClass: 'dark-scheme' },
      component: StaffLogin
    },
    {
      path: '/process-complete',
      name: 'process-complete',
      component: ProcessComplete
    }
  ]
})



