<template>
  <div class="full-container">
    <div class="verify-req">
      <h1>Enter staff passcode</h1>
      <p>Enter your four digit pincode</p>
      <div class="progression">
        <div
          v-for="index in inputCount"
          v-bind:key="index"
          :class="`progress-icon icon-${index}`"
          ref="icons"
          ></div>
        </div>

      <input
        type="password"
        id="pwd"
        ref="pwd"
        maxlength="4"
        v-on:keyup="updateInput()"
        autofocus
        class="password-input"
        />
      <router-link :to="'review-member'" class="btn-default">login</router-link>
    </div>
  </div>
</template>

<script>

export default {
  name: 'StaffLogin',
  methods: {
    updateInput () {
      const pwd = this.$refs.pwd.value
      const inputCharCount = pwd.length
      for (let i = 0; i < inputCharCount; i++) {
        this.$refs.icons[i].classList.add('complete')
      }
      if (inputCharCount === this.inputCount) {
        this.verifyStaff()
      }
    },
    verifyStaff () {
      console.log('TODO: send request with staff creds')
      // if (request sucessfull) {
      //   // got to all done page
      // } else {
      //   this.reset()
      // }
    },
    reset () {
      this.$refs.pwd.focus()
      for (let i = 0; i < this.inputCount; i++) {
        this.$refs.icons[i].classList.remove('complete')
      }
    }
  },
  data () {
    return {
      inputLocaction: 1,
      inputCount: 4
    }
  }
}
</script>

<style scoped lang="scss">
  .password-input {
    position: absolute;
    left: -999999px
  }

  .verify-req {
    text-align: center;
    width: 100%;
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .progression {
    width: 180px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .progress-icon {
    width: 25px;
    height: 25px;
    border: 1px solid $form-bg;
    border-radius: 50%;
  }

  .progress-icon.complete {
    background: $form-bg;
  }
</style>



