<template>
  <div id="app">
    <router-view name="pageNav" />
    <router-view/>
  </div>
</template>

<script>
import axios from 'axios'
const LOGIN_POINT = '/authorization/login'
const REF_POINT = '/referencedata'
// TODO: get this env url dynamically
const ENV_URL = 'https://mrcplgmemberformstest.azurewebsites.net/api'
export default {

  name: 'App',
  mounted () {
    this.login().then((r) => {
      console.log(r)
      this.getReferenceData()
    })
  },
  methods: {
    login () {
      // TODO: move to login component
      return axios({
        method: 'post',
        url: ENV_URL + LOGIN_POINT,
        dataType: 'json',
        headers: {
          'Content-Type': 'application/json'
        },
        data: JSON.stringify('0001')
      })
        .then(({ data }) => {
          return true
        })
        .catch(err => {
          console.error('error', err)
        })
    },
    getReferenceData () {
      axios.get(ENV_URL + REF_POINT)
        .then(({ data }) => {
          console.log(data)
        })
        .catch(err => {
          console.error('error', err)
        })
    }
  }
}
</script>

<style lang="scss">
  @import '../node_modules/bootstrap/scss/bootstrap.scss';
  @import './assets/styles/global.scss';
</style>

<style scoped>
  #app {
    height: 100%;
    min-height: 100vh;
  }
</style>



